html {
  font-size: 100%;
}

html,
body {
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

* {
  box-sizing: border-box;
}

p,
hr {
  margin: 0;
}

input,
select {
  font-size: inherit;
}

.wrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100vh;
  overflow: hidden;
  padding-inline: 15px;
}

.container {
  max-width: 1024px;
  margin: 0 auto;
}

main {
  width: 100%;
  margin-block-start: 120px;
  flex-grow: 1;
}

footer, .header {
  flex: 0 0 100px;
}

.footer-block {
  height: 100%;
}

.footer-block__text {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.convertor-group {
  display: flex;
  gap: 10px;
}

@media (max-width: 600px) {
  .convertor-group {
    flex-direction: column;
  }
}

.custom-input {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.custom-input input {
  border: none;
  border-bottom: solid 1px;
}

.custom-select {
  border: none;
  border-bottom: solid 1px;
}

.convertor {
  display: flex;
  gap: 20px;
  justify-content: center;
}

@media (max-width: 368px) {
  .convertor {
    flex-direction: column;
  }
}

table {
  min-width: 500px;
  margin-block-end: 10px;
}

table,
th,
td {
  border: 1px solid rgba(224, 224, 224, 1);
}

th,
td {
  width: 33.333%;
}

td {
  position: relative;
}

.table-input {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}

.table-input input {
  width: 100%;
  height: 100%;
  text-align: center;
  font-size: inherit;
  border: none;
}

.table-input input:focus {
  outline: 1px solid lightblue;
}

.table-input__actions {
  position: absolute;
  background: white;
  right: 0px;
  top: -10px;
  display: flex;
  gap: 1px;
  border: 1px solid rgba(0 0 0 / 20%);
}

.editable-cell {
  position: relative;
}

.editable-cell__actions {
  display: none;
}

.editable-cell:hover .editable-cell__actions {
  position: absolute;
  right: 0;
  top: 0;
  display: flex;
  border: 1px solid rgba(0 0 0 / 20%);
}
